@font-face {
  font-family: Avenir;
  font-weight: 400;
  src: url("fonts/Avenir-Book.ttf");
}

@font-face {
  font-family: Avenir;
  font-weight: 900;
  src: url("fonts/Avenir-Heavy.ttf");
}

* {
  box-sizing: border-box;
}

html {
  font-size: 16px;
  font-weight: 300;
  font-family: Avenir Arial, Helvetica, sans-serif;
}

.form {
  margin: 20px 0 0;
  position: relative;
  background: #fff;
  box-shadow: 0 1px 8px rgba(61, 78, 97, 0.1);
  border: 1px solid #e1e7ed;
  border-radius: 4px;
}

.form:hover {
  box-shadow: 0 3px 8px rgba(61, 78, 97, 0.2);
}

.form__img {
  position: absolute;
  cursor: pointer;
}

.form__img--big {
  top: 25px;
  left: 25px;
}

.form__img--small {
  top: 11px;
  left: 12px;
}

.form__img-big-inner {
  height: 19px;
  width: 19px;
}

.form__img-small-inner {
  height: 11px;
  width: 11px;
}

.form__seach {
  width: 100%;
  border: none;
  color: #3d4e61;
  font-family: Avenir, sans-serif;
  /* font-family: inherit; - так тесты не проходит */
  font-weight: 400;
  outline: none;
  border-radius: 3px;
}

.form__seach--big {
  font-size: 16px;
  padding: 23px 62px;
  line-height: 22px;
}

.form__seach--small {
  font-size: 14px;
  padding: 10px 33px 11px;
  line-height: 19px;
}

.form__seach:focus {
  font-weight: 900;
  background: linear-gradient(180deg, #fff 0%, #f6f6f7 100%);
}
